@import "./styles/main.scss";

@font-face {
  font-family: Leon;
  src: url("/assets/fonts/Leon-Regular_TRIAL.ttf"); 
}

@font-face {
  font-family: Montserrat;
  src: url("/assets/fonts/Montserrat-Regular.ttf");
}

html {
  width: 100vw;
  overflow-x: hidden; 
}

body {
  background-color: #111;
  color: white;
  font-family: Montserrat, sans-serif;
  margin: 0;
}

.body-content {
  margin: 0 auto;
  max-width: 90%;
  left: -10px; 
}

select,
textarea {
  width: 100%;
  font-family: Montserrat, sans-serif;
  font-size: 14px;
  resize: none; 
}

form input {
  width: auto;
  font-family: Montserrat, sans-serif;
  font-size: 14px; 
}

form .button {
  width: auto;
  float: right; 
}

.contact-form {
  padding: 2px; 
}

.message {
  color: #FFD603; 
}

.error {
  color: red; 
}

.corner-controls {
  position: fixed;
  right: 1.5em;
  bottom: 1em;
  font-size: 16px;
  text-align: right; 
}

.corner-controls a {
  padding-top: 10px;
  display: block; 
}

ul {
  list-style: square; 
}

h1 {
  font-size: 1.5vmax; 
}

h2 {
  font-size: 1.1vmax; 
}

h3 {
  font-size: 0.9vmax; 
}

a {
  color: #FF9A03;
  text-decoration: none; 
}

a:hover,
a:focus {
  color: #FFD603;
  text-decoration: none; 
}

p {
  font-size: 0.75vmax; 
}

.darken-text {
  color: #AAA; 
}

.text-center {
  text-align: center; 
}

.btn a {
  vertical-align: middle; 
}

@media screen and (max-width: 1382px) {
  p {
    font-size: 1.2em; 
}
  h1 {
    font-size: 1.8em; 
}
  h2 {
    font-size: 1.6em; 
}
  h3 {
    font-size: 1.4em; 
}
  h3 {
    font-size: 1.2em; } 
}

